.App {
  text-align: center;
}

.App-header {
  background-color: #007bff;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 4vmin);
  color: white;
}
.Form{
  width: 30%;
  margin-top: 50px;
}

.List{
  width: 68%;
  margin-top: 50px;
}

.Containers{
   display: flex;
   flex-direction: row;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
